import dbConnect from "@/lib/mongodb";
import { Account, Loan } from "@/lib/models";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { formatCurrency, formatDate } from "@/lib/utils/emi-calculator";
import { LoanActionsDropdown } from "@/components/admin/loan-actions-dropdown";
import {
  Clock,
  CheckCircle2,
  XCircle,
  AlertCircle,
  DollarSign,
} from "lucide-react";

export default async function AdminLoansPage() {
  await dbConnect();

  // ✅ FIX: Fetch loans without populate (userId is a string, not ObjectId)
  const loansDocs = await Loan.find({}).sort({ createdAt: -1 }).lean();

  // Serialize loans
  const loans = loansDocs.map((loan: any) => ({
    id: loan._id.toString(),
    userId: loan.userId, // This is Clerk ID string
    loanType: loan.loanType,
    amount: loan.amount,
    interestRate: loan.interestRate,
    tenureMonths: loan.tenureMonths,
    emiAmount: loan.emiAmount,
    totalPayable: loan.totalPayable,
    amountPaid: loan.amountPaid || 0,
    remainingAmount: loan.remainingAmount || loan.totalPayable,
    status: loan.status,
    disbursementAccountId: loan.disbursementAccountId?.toString(),
    createdAt: loan.createdAt.toISOString(),
  }));

  // ✅ FIX: Fetch all active accounts
  const accountsDocs = await Account.find({ status: "active" }).lean();

  const accounts = accountsDocs.map((acc: any) => ({
    id: acc._id.toString(),
    userId: acc.userId, // Clerk ID string
    accountNumber: acc.accountNumber,
    accountType: acc.accountType,
    balance: acc.balance,
    status: acc.status,
  }));

  // Stats
  const pendingCount = loans.filter((l) => l.status === "pending").length;
  const approvedCount = loans.filter((l) =>
    ["approved", "disbursed", "active"].includes(l.status)
  ).length;
  const rejectedCount = loans.filter((l) => l.status === "rejected").length;
  const totalDisbursed = loans
    .filter((l) => ["disbursed", "active", "closed"].includes(l.status))
    .reduce((sum, l) => sum + Number(l.amount), 0);

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "pending":
        return (
          <Badge variant="secondary">
            <Clock className="mr-1 h-3 w-3" />
            Pending
          </Badge>
        );
      case "approved":
        return (
          <Badge className="bg-primary">
            <CheckCircle2 className="mr-1 h-3 w-3" />
            Approved
          </Badge>
        );
      case "rejected":
        return (
          <Badge variant="destructive">
            <XCircle className="mr-1 h-3 w-3" />
            Rejected
          </Badge>
        );
      case "disbursed":
      case "active":
        return (
          <Badge className="bg-accent text-accent-foreground">
            <CheckCircle2 className="mr-1 h-3 w-3" />
            Active
          </Badge>
        );
      case "closed":
        return (
          <Badge variant="outline">
            <CheckCircle2 className="mr-1 h-3 w-3" />
            Closed
          </Badge>
        );
      case "defaulted":
        return (
          <Badge variant="destructive">
            <AlertCircle className="mr-1 h-3 w-3" />
            Defaulted
          </Badge>
        );
      default:
        return <Badge variant="outline">{status}</Badge>;
    }
  };

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-foreground">Loan Management</h1>
        <p className="text-muted-foreground">
          Review and manage loan applications
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid gap-4 sm:grid-cols-4">
        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-warning/10">
                <Clock className="h-5 w-5 text-warning" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Pending</p>
                <p className="text-xl font-bold text-warning">{pendingCount}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-accent/10">
                <CheckCircle2 className="h-5 w-5 text-accent" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Active</p>
                <p className="text-xl font-bold text-accent">{approvedCount}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-destructive/10">
                <XCircle className="h-5 w-5 text-destructive" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Rejected</p>
                <p className="text-xl font-bold text-destructive">
                  {rejectedCount}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="border-0 shadow-sm">
          <CardContent className="p-4">
            <div className="flex items-center gap-3">
              <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-primary/10">
                <DollarSign className="h-5 w-5 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Disbursed</p>
                <p className="text-xl font-bold text-primary">
                  {formatCurrency(totalDisbursed)}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Loans Table */}
      <Card className="border-0 shadow-sm">
        <CardHeader>
          <CardTitle>All Loans</CardTitle>
        </CardHeader>
        <CardContent>
          {loans.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-border">
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      User ID
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Type
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Amount
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Rate
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Tenure
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      EMI
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Status
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-medium text-muted-foreground">
                      Applied
                    </th>
                    <th className="px-4 py-3 text-right text-sm font-medium text-muted-foreground">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-border">
                  {loans.map((loan) => (
                    <tr key={loan.id} className="hover:bg-muted/50">
                      <td className="px-4 py-4">
                        <p className="font-mono text-xs text-muted-foreground">
                          {loan.userId.substring(0, 12)}...
                        </p>
                      </td>
                      <td className="px-4 py-4 capitalize">{loan.loanType}</td>
                      <td className="px-4 py-4 font-semibold">
                        {formatCurrency(Number(loan.amount))}
                      </td>
                      <td className="px-4 py-4">{loan.interestRate}%</td>
                      <td className="px-4 py-4">{loan.tenureMonths}m</td>
                      <td className="px-4 py-4">
                        {formatCurrency(Number(loan.emiAmount))}
                      </td>
                      <td className="px-4 py-4">
                        {getStatusBadge(loan.status)}
                      </td>
                      <td className="px-4 py-4 text-sm text-muted-foreground">
                        {formatDate(new Date(loan.createdAt))}
                      </td>
                      <td className="px-4 py-4 text-right">
                        <LoanActionsDropdown
                          loanId={loan.id}
                          loanStatus={loan.status}
                          userAccounts={accounts.filter(
                            (a) => a.userId === loan.userId
                          )}
                        />
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="text-center py-12 text-muted-foreground">
              No loan applications yet.
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
